import { Button, Group, Modal, NumberInput, Stack } from "@mantine/core";
import { DateInput, DatePicker, DatesProvider } from "@mantine/dates";
import { IconCalendar } from "@tabler/icons-react";
import "dayjs/locale/es";
import moment from "moment";
import { useEffect, useState } from "react";

const NewMeasurementModal = ({ _measurement, isOpen, onClose, type }) => {
    const [measurement, setMeasurement] = useState();

    useEffect(() => {
        if (isOpen) {
            setMeasurement(
                _measurement || {
                    value: 0,
                    date: moment().format(),
                    medidorId: type,
                }
            );
        }
    }, [_measurement, isOpen]);

    const save = () => onClose(measurement);

    const isValid = measurement && measurement.value > 0 && measurement.date;

    return (
        <Modal opened={isOpen} onClose={onClose} title="Agregar medicion">
            {measurement && (
                <Stack>
                    <NumberInput
                        label="Valor del medidor (kWh)"
                        value={measurement.value}
                        onChange={(v) =>
                            setMeasurement({
                                ...measurement,
                                value: v,
                            })
                        }
                    />
                    <DateInput
                        valueFormat="DD MMMM YYYY"
                        label="Fecha de la medicion"
                        value={moment(measurement.date).toDate()}
                        onChange={(ndate) =>
                            setMeasurement({
                                ...measurement,
                                date: moment(ndate).format(),
                            })
                        }
                        rightSection={<IconCalendar />}
                    />
                    <Group justify="right">
                        <Button variant="transparent" onClick={() => onClose()}>
                            Cancelar
                        </Button>
                        <Button disabled={!isValid} onClick={save}>
                            Guardar
                        </Button>
                    </Group>
                </Stack>
            )}
        </Modal>
    );
};

export default NewMeasurementModal;
