import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import SimpleCrypto from "simple-crypto-js";
import {
    getFirestore,
    collection,
    getDocs,
    writeBatch,
    doc,
    setDoc,
    deleteDoc,
} from "firebase/firestore/lite";

const encryptedConfig = ``;

const DB = new (function () {
    let self = this;
    this.app = undefined;
    this.db = undefined;
    this.errorCallback = undefined;

    self.connect = (password, _errorCallback) => {
        const simpleCrypto = new SimpleCrypto(password);
        const decrypted = simpleCrypto.decrypt(encryptedConfig);
        this.errorCallback = _errorCallback;
        console.log(decrypted);
        self.app = initializeApp(decrypted);
        self.db = getFirestore(self.app);
    };

    self.getMeasurements = async () => {
        try {
            const measurements = collection(self.db, "measurements");
            const snapshot = await getDocs(measurements);
            return snapshot.docs.map((doc) => ({ ...doc.data(), id: doc.id }));
        } catch (err) {
            self.errorCallback();
        }
    };
    self.getBills = async () => {
        try {
            const bills = collection(self.db, "bills");
            const snapshot = await getDocs(bills);
            return snapshot.docs.map((doc) => ({ ...doc.data(), id: doc.id }));
        } catch (err) {
            self.errorCallback();
        }
    };

    self.saveMeasurement = async (id, measure) => {
        try {
            const medicionRef = doc(collection(self.db, "measurements"), id);
            await setDoc(medicionRef, measure);
        } catch (err) {
            self.errorCallback();
        }
    };

    self.saveBill = async (id, bill) => {
        try {
            const billRef = doc(collection(self.db, "bills"), id);
            await setDoc(billRef, bill);
        } catch (err) {
            self.errorCallback();
        }
    };

    self.deleteMeasurement = async (id) => {
        try {
            console.log("Deleting", id);
            const medicionRef = doc(collection(self.db, "measurements"), id);

            await deleteDoc(medicionRef);
        } catch (err) {
            self.errorCallback();
        }
    };

    self.encode = (obj, password) => {
        const simpleCrypto = new SimpleCrypto(password);
        console.log(simpleCrypto.encrypt(JSON.stringify(obj), password));
    };
})();

export default DB;
