import {
    Box,
    Button,
    Card,
    Center,
    Container,
    Grid,
    Modal,
    Group,
    Loader,
    LoadingOverlay,
    NumberInput,
    ScrollArea,
    Stack,
    Table,
    Tabs,
    Text,
} from "@mantine/core";
import { DateInput, DatePicker, DatesProvider } from "@mantine/dates";
import { IconCalendar, IconCurrencyDollar } from "@tabler/icons-react";
import "dayjs/locale/es";
import moment from "moment";
import { useEffect, useState } from "react";
import { getFixedCharges } from "../srv/service";

const defFixedCosts = [
    "Costo distr energia",
    "Costo alumbrado publico",
    "Costo distr. Agua",
    "Transp. agua potable",
    "Costo distr. cloacas",
    "Tasa bomberos voluntarios",
    "Tasa pago deuda O8965",
    "Otro (Opcional)",
];

export const NewBillModal = ({ _bill, isOpen, onClose }) => {
    const [bill, setBill] = useState();

    useEffect(() => {
        if (isOpen) {
            setBill(
                _bill || {
                    amount: 0,
                    from: moment().format(),
                    to: moment().format(),
                    fixedCosts: {},
                    createdAt: moment().format(),
                }
            );
        }
    }, [_bill, isOpen]);

    const save = () => onClose(bill);

    const isValid = bill && bill.amount > 0 && bill.from && bill.to;

    return (
        <Modal
            opened={isOpen}
            onClose={onClose}
            title="Agregar medicion"
            size={"lg"}
        >
            {bill && (
                <Stack>
                    <Text fw={700}>Factura</Text>
                    <Grid>
                        <Grid.Col span={{ xs: 12, sm: 4 }}>
                            <DateInput
                                value={bill.from && moment(bill.from).toDate()}
                                leftSection={<IconCalendar />}
                                valueFormat="DD MMMM YYYY"
                                clearable
                                onChange={(date) =>
                                    setBill({
                                        ...bill,
                                        from: date && moment(date).format(),
                                    })
                                }
                                label="Medición desde"
                            />
                        </Grid.Col>
                        <Grid.Col span={{ xs: 12, sm: 4 }}>
                            <DateInput
                                value={bill.to && moment(bill.to).toDate()}
                                leftSection={<IconCalendar />}
                                valueFormat="DD MMMM YYYY"
                                clearable
                                onChange={(date) =>
                                    setBill({
                                        ...bill,
                                        to: date && moment(date).format(),
                                    })
                                }
                                label="Medición hasta"
                            />
                        </Grid.Col>
                        <Grid.Col span={{ xs: 12, sm: 4 }}>
                            <NumberInput
                                value={bill.amount}
                                leftSection={<IconCurrencyDollar />}
                                onChange={(amount) =>
                                    setBill({
                                        ...bill,
                                        amount,
                                    })
                                }
                                label="Valor factura"
                            />
                        </Grid.Col>
                        <Grid.Col span={{ xs: 12 }}>
                            <Text fw={700}>Montos fijos</Text>
                        </Grid.Col>
                        {defFixedCosts.map((df) => (
                            <Grid.Col key={df} span={{ xs: 12, sm: 6 }}>
                                <NumberInput
                                    key={df}
                                    value={bill.fixedCosts[df]}
                                    leftSection={<IconCurrencyDollar />}
                                    label={df}
                                    onChange={(val) =>
                                        setBill({
                                            ...bill,
                                            fixedCosts: {
                                                ...bill.fixedCosts,
                                                [df]: val,
                                            },
                                        })
                                    }
                                />
                            </Grid.Col>
                        ))}
                    </Grid>
                    <Text fw={700} fz={18}>
                        Total gastos fijos $: {getFixedCharges(bill.fixedCosts)}
                    </Text>
                    <Group justify="right">
                        <Button onClick={save} disabled={!isValid}>
                            Guardar
                        </Button>
                    </Group>
                </Stack>
            )}
        </Modal>
    );
};
