import "./App.css";
import { getAccumulatedValue } from "./srv/service";
import {
    measurementsCasa,
    measurementsTalleres,
    measurementsVerdu,
} from "./data/measurements";
import { bills } from "./data/bills";
import {
    Alert,
    Box,
    Button,
    Container,
    Group,
    LoadingOverlay,
    Stepper,
    Text,
} from "@mantine/core";
import LoadValuesPage from "./pages/load-values-page";
import { useEffect, useState } from "react";
import DB from "./srv/firebase";
import { v4 as uuidv4 } from "uuid";
import { PinInpt } from "./components/pin-input";
import { useMediaQuery } from "@mantine/hooks";
import { IconArrowRight, IconLogin, IconRecharging } from "@tabler/icons-react";
import {
    IconExclamationCircle,
    IconExclamationMark,
} from "@tabler/icons-react";
import { LoadBillPage } from "./pages/load-bill";
import { CostReport } from "./pages/cost-report";
function App() {
    const [page, setPage] = useState(0);
    const [loading, setLoading] = useState(false);

    const [measurements, setMeasurements] = useState([]);
    const [bills, setBills] = useState([]);
    const [billToPrint, setBillToPrint] = useState();

    const [authError, setAuthError] = useState();
    const [error, setError] = useState();

    const isMobile = useMediaQuery("(max-width: 35.25em)");
    const isTablet = useMediaQuery("(max-width: 46.25em)");

    const saveMeasurement = async (measurement) => {
        setLoading(true);
        if (measurement.id) {
            setMeasurements(
                measurements.map((m) =>
                    m.id === measurement.id ? measurement : m
                )
            );
        } else {
            // new element
            measurement.id = uuidv4();
            setMeasurements([...measurements, measurement]);
        }
        await DB.saveMeasurement(measurement.id, measurement);

        setLoading(false);
    };

    const deleteMeasurement = async (id) => {
        setLoading(true);
        await DB.deleteMeasurement(id);
        setMeasurements(measurements.filter((m) => m.id !== id));
        setLoading(false);
    };

    const auth = async (pin) => {
        setLoading(true);
        try {
            DB.connect(pin, () => setError(true));
            const ms = await DB.getMeasurements();
            const bll = await DB.getBills();
            console.log(bll);
            setMeasurements(ms);
            setBills(bll);
            setPage(1);
        } catch (err) {
            if (err.message.includes("Invalid encrypted")) {
                setAuthError("Pin inválido");
            } else if (err.message.includes("Request failed")) {
                setAuthError("Error al conectarse");
            }
            console.log(err.message);
        }

        setLoading(false);
    };

    const saveBill = async (bill) => {
        setLoading(true);
        if (bill.id) {
            setBills(bills.map((m) => (m.id === bill.id ? bill : m)));
        } else {
            // new element
            bill.id = uuidv4();
            setBills([...bills, bill]);
        }
        await DB.saveBill(bill.id, bill);

        setLoading(false);
    };

    const print = (bill) => {
        setBillToPrint(bill);
        setPage(3);
    };

    return (
        <Container p={"sm"}>
            <LoadingOverlay
                visible={loading}
                zIndex={1000}
                overlayProps={{ radius: "sm", blur: 1 }}
            />
            <Box>
                {error ? (
                    <Alert
                        title="Error"
                        icon={<IconExclamationCircle />}
                        color="red"
                    >
                        <Text>
                            Ocurrió un error. Verifique su conexión a internet y
                            actualice la página
                        </Text>
                    </Alert>
                ) : (
                    <Stepper active={page} wrap="nowrap">
                        <Stepper.Step
                            label={isMobile ? undefined : "Autenticación"}
                            description={
                                isTablet ? undefined : "Ingrese el PIN"
                            }
                            icon={<IconLogin />}
                        >
                            <PinInpt errorMsg={authError} onSetPin={auth} />
                        </Stepper.Step>
                        <Stepper.Step
                            icon={<IconRecharging />}
                            label={isMobile ? undefined : "Mediciones"}
                            description={
                                isTablet
                                    ? undefined
                                    : "Verifique y cargue mediciones"
                            }
                        >
                            <LoadValuesPage
                                measurements={measurements}
                                loading={loading}
                                removeMeasurement={deleteMeasurement}
                                saveMeasurement={saveMeasurement}
                                buttons={
                                    <Group justify="right" w={"100%"}>
                                        <Button
                                            onClick={() => setPage(2)}
                                            rightSection={<IconArrowRight />}
                                        >
                                            Continuar
                                        </Button>
                                    </Group>
                                }
                            />
                        </Stepper.Step>
                        <Stepper.Step
                            label={isMobile ? undefined : "Factura"}
                            description={
                                isTablet
                                    ? undefined
                                    : "Ingrese los datos de la factura"
                            }
                        >
                            <LoadBillPage
                                bills={bills}
                                saveBill={saveBill}
                                printBill={print}
                            />
                        </Stepper.Step>
                        <Stepper.Completed>
                            <CostReport bill={billToPrint} />
                        </Stepper.Completed>
                    </Stepper>
                )}
            </Box>
        </Container>
    );
}

export default App;
