import {
    Box,
    Button,
    Card,
    Center,
    Container,
    Grid,
    Group,
    Loader,
    LoadingOverlay,
    NumberInput,
    ScrollArea,
    Stack,
    Table,
    Tabs,
    Text,
} from "@mantine/core";
import { useState } from "react";
import { DateInput, DatePicker, DatesProvider } from "@mantine/dates";
import moment from "moment";
import {
    IconCurrencyDollar,
    IconCalendar,
    IconPencil,
    IconTrash,
    IconPrinter,
    IconCash,
} from "@tabler/icons-react";
import "moment/locale/es"; // without this line it didn't work
import "dayjs/locale/es";
import { NewBillModal } from "../components/new-bill";
import { getFixedCharges, timeAgo } from "../srv/service";

moment.locale("es");

export const LoadBillPage = ({ bills, saveBill, printBill }) => {
    const [open, setOpen] = useState(false);
    const [billToEdit, setBillToEdit] = useState(undefined);

    const close = async (bill) => {
        if (bill) {
            await saveBill(bill);
        }
        setOpen(false);
        setBillToEdit(undefined);
    };

    const edit = (b) => {
        setBillToEdit(b);
        setOpen(true);
    };
    return (
        <Card shadow="md" withBorder bg={"#fafafa"}>
            <NewBillModal _bill={billToEdit} isOpen={open} onClose={close} />
            <Stack>
                <Button onClick={() => setOpen(true)}>Agregar factura</Button>
                <ScrollArea h={250} bg={"#f2f2f2"}>
                    <Table styles={{ table: { whiteSpace: "nowrap" } }}>
                        <Table.Thead
                            styles={{ thead: { position: "sticky" } }}
                            bg={"#dfdfdf"}
                        >
                            <Table.Tr>
                                <Table.Th>Total Factura</Table.Th>
                                <Table.Th>Gastos Fijos</Table.Th>
                                <Table.Th colSpan={2}>Creación</Table.Th>
                            </Table.Tr>
                        </Table.Thead>
                        <Table.Tbody>
                            {bills
                                .sort(
                                    (a, b) =>
                                        moment(b.createdAt).toDate().getTime() -
                                        moment(a.createdAt).toDate().getTime()
                                )
                                .map((m, idx) => (
                                    <Table.Tr key={idx}>
                                        <Table.Td>
                                            <Text>${m.amount}</Text>
                                            <Text fz={10} c={"#555"}>
                                                {moment(m.from).format(
                                                    "D [de] MMMM"
                                                )}{" "}
                                                al{" "}
                                                {moment(m.to).format(
                                                    "D [de] MMMM"
                                                )}
                                            </Text>
                                        </Table.Td>

                                        <Table.Td>
                                            <Text>
                                                ${getFixedCharges(m.fixedCosts)}
                                            </Text>
                                        </Table.Td>
                                        <Table.Td>
                                            <Text>{timeAgo(m.createdAt)}</Text>
                                        </Table.Td>
                                        <Table.Td w={0}>
                                            <Group wrap="nowrap">
                                                <Button
                                                    p={0}
                                                    variant="transparent"
                                                    onClick={() => edit(m)}
                                                    px={"xs"}
                                                >
                                                    <IconPencil />
                                                </Button>
                                                <Button
                                                    p={0}
                                                    variant="transparent"
                                                    onClick={() => printBill(m)}
                                                    px={"xs"}
                                                >
                                                    <IconPrinter />
                                                </Button>
                                            </Group>
                                        </Table.Td>
                                    </Table.Tr>
                                ))}
                        </Table.Tbody>
                    </Table>
                </ScrollArea>
            </Stack>
        </Card>
    );
};
