import moment from "moment";
import TimeAgo from "javascript-time-ago";
import es from "javascript-time-ago/locale/es";
TimeAgo.addDefaultLocale(es);

const tAgo = new TimeAgo("es-ES");

export const getInterpolatedValue = (measurementsList, dateInput) => {
    const inputMoment = moment(dateInput);

    let prevDate, nextDate;

    for (let i = 0; i < measurementsList.length - 1; i++) {
        const curr = measurementsList[i].date;
        const next = measurementsList[i + 1].date;

        if (
            moment(curr)
                .startOf("day")
                .isSameOrBefore(inputMoment.startOf("day")) &&
            moment(next)
                .startOf("day")
                .isSameOrAfter(inputMoment.startOf("day"))
        ) {
            prevDate = measurementsList[i];
            nextDate = measurementsList[i + 1];
        }
    }

    if (!prevDate || !nextDate) {
        throw new Error(
            "La fecha a interpolar no se encuentra entre 2 fechas registradas "
        );
    }

    const diffDaysTotal = moment(prevDate.date).diff(
        moment(nextDate.date),
        "days"
    );
    const eachDayValue = (nextDate.value - prevDate.value) / diffDaysTotal;

    const daysToDate = moment(prevDate.date).diff(inputMoment, "days");

    return prevDate.value + eachDayValue * daysToDate;
};

export const getAccumulatedValue = (measurementsList, startDate, endDate) => {
    return (
        getInterpolatedValue(measurementsList, endDate) -
        getInterpolatedValue(measurementsList, startDate)
    );
};
function isNumber(n) {
    return !isNaN(parseFloat(n)) && !isNaN(n - 0);
}

export const getFixedCharges = (fixedCharges) => {
    let total = 0;
    for (const [key, value] of Object.entries(fixedCharges)) {
        if (isNumber(value)) {
            total += parseFloat(value);
        }
    }
    return total;
};

export const timeAgo = (date) => {
    if (!date) {
        return "??";
    }
    return tAgo.format(moment(date).toDate());
};
